import en_US from './lang/en_US';
import zh_CN from './lang/zh_CN';
/**
 * All locales supported. en_US, zh_CN is build-in
 * @type {{en: function(*, *), zh_CN: function(*, *)}}
 */

var Locales = {
  en_US: en_US,
  zh_CN: zh_CN
};
/**
 * register a locale
 * @param locale
 * @param func
 */

export var register = function register(locale, func) {
  Locales[locale] = func;
};
/**
 * get a locale, default is en_US
 * @param locale
 * @returns {*}
 */

export var getLocale = function getLocale(locale) {
  return Locales[locale] || en_US;
};